// Helper Functions
const getPhaseColor = (phaseId) => {
  return colors.phase[phaseId] || colors.phase[0];
};

const getDifficultyColor = (phaseId) => {
  if (phaseId <= 1) return colors.difficulty.beginner;
  if (phaseId <= 3) return colors.difficulty.intermediate;
  if (phaseId <= 5) return colors.difficulty.advanced;
  return colors.difficulty.expert;
};

const getDifficultyLabel = (phaseId) => {
  if (phaseId <= 1) return 'Beginner';
  if (phaseId <= 3) return 'Intermediate';
  if (phaseId <= 5) return 'Advanced';
  return 'Expert';
};

const getTimeEstimate = (phaseId) => {
  const estimates = {
    0: '2-3 months',
    1: '3-4 months',
    2: '3-4 months',
    3: '2-3 months',
    4: '2-3 months',
    5: '1-2 months',
    6: 'Ongoing',
  };
  return estimates[phaseId] || '2-3 months';
};

